import { Component, Input } from "@angular/core";
import { BehaviorSubject, combineLatest, Observable } from "rxjs";
import { map } from "rxjs/operators";
import { ApiLocalText } from "src/app/api";
import { LanguageService } from "src/app/services";

@Component({
  selector: "app-local-pill",
  templateUrl: "./local-pill.component.html",
  styleUrls: ["./local-pill.component.scss"],
})
export class LocalPillComponent {
  private readonly names$ = new BehaviorSubject<ApiLocalText[]>([]);
  public readonly local$: Observable<ApiLocalText>;
  public readonly neededLanguages$: Observable<string[]>;

  @Input()
  public set names(value: ApiLocalText[]) {
    this.names$.next(value);
  }

  public constructor(languageService: LanguageService) {
    this.local$ = combineLatest([languageService.languages$, this.names$]).pipe(
      map(([languages, names]) => {
        for (const language of languages) {
          for (const name of names) {
            if (name.language === language && !!name.value) {
              return name;
            }
          }
        }
        return null;
      }),
    );

    this.neededLanguages$ = combineLatest([
      languageService.languages$,
      this.names$,
    ]).pipe(
      map(([languages, names]) => {
        return languages.filter(
          (l) => !names.some((n) => n.language === l && !!n.value),
        );
      }),
    );
  }
}
