import { Component } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { Observable } from "rxjs";
import { map, shareReplay, switchMap } from "rxjs/operators";
import {
  ApiContributionInfo,
  ApiProductResponse,
  ProductsApiService,
} from "src/app/api";
import { marker } from "@biesbjerg/ngx-translate-extract-marker";
import { TimeAgoPipe } from "ngx-moment";
import * as moment from "moment";

marker("contribution.category.allergen");
marker("contribution.category.ingredients");
marker("contribution.category.ingredient-create");
marker("contribution.category.created");
marker("contribution.category.photo");

interface ApiContributionInfoWithCount extends ApiContributionInfo {
  count: number;
}

@Component({
  selector: "app-product",
  templateUrl: "./product.page.html",
  styleUrls: ["./product.page.scss"],
})
export class ProductPage {
  public readonly item$: Observable<ApiProductResponse>;
  public readonly contributions$: Observable<ApiContributionInfoWithCount[]>;

  public constructor(
    activatedRoute: ActivatedRoute,
    productsApiService: ProductsApiService,
    timeAgoPipe: TimeAgoPipe,
  ) {
    this.item$ = activatedRoute.paramMap.pipe(
      map((p) => p.get("id")),
      switchMap((id) =>
        id === "ean"
          ? productsApiService.productsGetProductByEan({
              ean: activatedRoute.snapshot.queryParamMap.get("ean"),
            })
          : productsApiService.productsGetProduct({ productGuid: id }),
      ),
      shareReplay(1),
    );

    this.contributions$ = this.item$.pipe(
      map((i) => {
        if (!i?.contributions) {
          return null;
        }
        const result: ApiContributionInfoWithCount[] = [];
        let previous: ApiContributionInfoWithCount;
        let previousTime: string;
        i.contributions.forEach((c) => {
          const time = timeAgoPipe.format(moment(c.instant));
          if (
            time !== previousTime ||
            ["remark"].includes(c.action) ||
            c.action !== previous?.action ||
            c.userId !== previous?.userId ||
            (c.description ?? "") !== (previous?.description ?? "")
          ) {
            previous = { ...c, count: 0 };
            result.push(previous);
            previousTime = time;
          }
          previous.count++;
        });
        return result;
      }),
    );
  }
}
